useCase/ILogin\";\nimport { IViewModel } from \"../../../domain/viewModel/IViewModel\";\nimport { IRouter, routeDefinition } from \"../../../domain/presentation/IRouter\";\nimport { INotificationInteractor, INotificationOptions } from \"../../../domain/model/INotificationOptions\";\nimport { INotification } from \"../../../domain/presentation/INotification\";\nimport { IViewModelManager } from \"../../../domain/viewModel/IViewModelManager\";\nimport { IDialog } from \"../../../domain/presentation/IDialog\";\nimport { IEditorManager } from \"../../../domain/objects/IEditorManager\";\nimport { IAutoLogin } from \"../../../domain/useCase/IAutoLogin\";\nimport { ILocalization } from \"../../../domain/infrastructure/ILocalization\";\n\nrouteDefinition({ noAuth: true });\nlet viewModel = container.resolve<IViewModel>(IViewModel);\nconst config = container.resolve<IConfig>(IConfig);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst autoLogin = container.resolve<IAutoLogin>(IAutoLogin);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst version = config.getVersion();\n\n\nlet email = ref(\"\");\nlet password = ref(\"\");\nlet showDomains = ref(true);\nlet showPassword = ref(true);\nlet showLoginButton = ref(true);\nlet domain = ref(viewModel.user);\nlet domainDisable = ref(true);\nlet showPlaceholder = ref(false);\nlet autologinData = autoLogin.gatherAutoLoginData();\nonMounted(() => {\n    document.getElementById(\"Loginpassword\")?.addEventListener(\"keyup\", passwordKeyup);\n    if (autologinData.email) {\n        email.value = autologinData.email;\n        setTimeout(() => validation()); // trigger\n    }\n    if (autologinData.pass) {\n        password.value = autologinData.pass;\n    }\n});\nfunction passwordKeyup(e: any) {\n    if (e.keyCode === 13) {\n        (<any>document).getElementById(\"login-button\").click();\n    }\n}\n\nfunction getDomains() {\n    executor.execute(\n        async () => {\n            await executor.executeUseCase<IGetUserDomains>(IGetUserDomains, { email: email.value });\n            showDomains.value = true;\n            autoLogin.trySetDomain(autologinData);\n            if (autoLogin.allSystemsGo(autologinData)) {\n                login();\n            }\n        },\n        { loading: true }\n    );\n}\n\nfunction nextStep() {\n    showPassword.value = true;\n    showLoginButton.value = true;\n}\nfunction passVisibility() {\n    let getType = (<any>document.getElementById(\"Loginpassword\")).type;\n    if (getType == \"password\") {\n        (<any>document.getElementById(\"Loginpassword\")).type = \"text\";\n        (<any>document.getElementById(\"passVisibility\")).style = \"color:white\";\n    } else {\n        (<any>document.getElementById(\"Loginpassword\")).type = \"password\";\n        (<any>document.getElementById(\"passVisibility\")).style = \"color:black\";\n    }\n}\nfunction validation() {\n    var email = (<any>document.getElementById(\"email\")).value;\n    var text = document.getElementById(\"text\");\n    var pattern = /^[^]+@[^ ]+\\.[a-z]{2,3}$/;\n    if (email.match(pattern)) {\n        text?.classList.remove(\"invalid\");\n        domainDisable.value = false;\n        showPlaceholder.value = true;\n        text!.innerHTML = \"\";\n        getDomains();\n    } else if (email == \"\") {\n        domainDisable.value = true;\n        showPlaceholder.value = false;\n        text?.classList.add(\"invalid\");\n        text!.innerHTML = \"Email is required\";\n        viewModel.user!.domains = [];\n    } else {\n        domainDisable.value = true;\n        showPlaceholder.value = false;\n        viewModel.user!.domains = [];\n        text?.classList.remove(\"valid\");\n        text?.classList.add(\"invalid\");\n        text!.innerHTML = \"Please Enter Valid Email Address\";\n    }\n}\nfunction login() {\n    executor.execute(\n        async () => {\n            await executor.executeUseCase<ILogin>(ILogin, {\n                username: email.value,\n                password: password.value,\n                domainID: viewModel.user!.selectedDomain!.ID,\n                redirectToMain: true,\n            });\n        },\n        { loading: true }\n    );\n}\n</script>\n<template>\n  <div class=\"login-container container-fluid p-0 m-0\">\n    <img src=\"../../../images/login_background.png\" alt=\"login_background\" class=\"login-background\">\n    <div class=\"form-wrap\">\n      <div class=\"form-content\">\n        <img src=\"../../../images/plateau_logo-red.svg\" alt=\"plateau_logo-red\" class=\"form-logo\">\n        <div class=\"login-text-container\">\n          <!-- <div>{{ version }}</div> -->\n          <h1 class=\"login-text\">Login</h1>\n        </div>\n        <p class=\"content\">{{localization.get(\"login_hailText\")}}</p>\n        <form action=\"javascript:void(0);\" id=\"form\">\n          <div class=\"input-group \">\n            <span class=\"input-style\" id=\"basic-addon1\"><i class=\"mdi mdi-email-outline mdi-24px\"></i></span>\n            <input class='form-control' type=\"text\" id='email' v-model=\"email\" placeholder=\"E-Mail\" required='true'\n              v-on:change=\"validation()\" />\n          </div>\n          <span id=\"text\"></span>\n          <br />\n          <div class=\"input-group mb-4\" v-if=\"showDomains\">\n            <span class=\"input-style\" id=\"basic-addon1\"><i class=\"mdi mdi-domain mdi-24px\"></i></span>\n            <select class='form-control list1'  :disabled='domainDisable'>\n              <option v-if='!showPlaceholder' value=\"\" disabled selected hidden type=\"text\">Domain</option>\n              <option v-if=\"viewModel.user\" v-for=\"domain in  viewModel.user!.domains\" :key=\"domain.ID\"\n                v-bind:value=\"domain\">\n                {{ domain.name }}\n              </option>\n            </select>\n          </div>\n          <div class=\"input-group mb-4\" v-if=\"viewModel.user?.selectedDomain || showPassword\">\n            <span class=\"input-style\" id=\"basic-addon1\"><i class=\"mdi mdi-lock-outline mdi-24px\"></i></span>\n            <input v-model=\"password\" type=\"password\" placeholder=\"Password\" class='form-control' id='Loginpassword' />\n            <button class=\"input-style\" id=\"passVisibility\"><i class=\"mdi mdi-eye mdi-24px eye\"\n                v-on:click=\"passVisibility()\"></i></button>\n          </div>\n          <!-- <div class=\"forgot-password\">\n            <a class=\"forgot-password-text\" href=\"#\">Forgot password</a>\n          </div> -->\n                    <div class=\"mb-4\">\n                        <div v-if=\"viewModel.user?.selectedDomain || showLoginButton\">\n                            <button @click=\"login()\" class=\"login-button\" id=\"login-button\">LOGIN</button>\n                        </div>\n                    </div>\n                </form>\n            </div>\n        </div>\n    </div>\n</template>\n\n<style></style>\n\n<style scoped>\n.form-wrap{\n  display: grid;\n  width: 100vh;\n  justify-content: center;\n  align-items: center;\n}\nbody{\n    margin: 0;\n    padding: 0;\n    overflow: hidden;\n}\n\n.login-background {\n    width: 60%;\n    height: 100vh;\n}\n\n.login-container {\n    display: flex;\n}\n\n.form-logo{\n    width: 276px;\n    height: 49px;\n    margin-bottom: 20px;\n    padding-right: -106px;\n    margin-left: 30px;\n}\n\n.login-text-container {\n    width: 80px;\n    margin-bottom: 16.5px;\n    border-bottom: 3px solid #151233;\n    opacity: 1;\n}\n\n.login-text {\n    margin-bottom: 1px;\n    text-align: left;\n    font-size: 32px;\n    font-family: \"Roboto\";\n    letter-spacing: 0px;\n    color: #151233;\n    opacity: 1;\n}\n\n.content {\n    text-align: left;\n    font-size: 22px;\n    font-family: \"Roboto\";\n    letter-spacing: 0px;\n    color: #757575;\n    opacity: 1;\n    margin-bottom: 41px;\n}\n\n.form-content {\n    margin-bottom: 28px;\n}\nselect.list1,\nselect:disabled {\n    background-color: #eeeeee !important;\n    border: none;\n    border-radius: 4px;\n    font-size: 20px;\n    font-family: \"Roboto\";\n    font-weight: normal;\n    letter-spacing: 0px;\n    color: #212121;\n    opacity: revert !important;\n}\ninput[type=\"text\"],\ninput[type=\"password\"] {\n    background-color: #eeeeee;\n    border-radius: 4px;\n    border: none;\n    font-size: 20px;\n    font-family: \"Roboto\";\n    font-weight: normal;\n    letter-spacing: 0px;\n    color: #212121;\n    opacity: 1;\n}\n.input-group {\n    position: relative;\n    display: flex;\n    flex-wrap: wrap;\n    align-items: stretch;\n    width: 100%;\n}\n\n.input-style {\n    border: none;\n    background-color: #eeeeee;\n    padding-top: 0.375rem;\n    padding-bottom: 0.375rem;\n    padding-left: 0.75rem;\n}\n\n.forgot-password-text {\n    font-size: 18px;\n    font-family: \"Roboto\";\n    font-weight: normal;\n    color: #151233;\n    text-decoration: none;\n}\n\n.forgot-password-text:hover {\n    color: #151233;\n}\n\n.forgot-password {\n    margin-top: -14px !important;\n    width: 100%;\n    text-align: right;\n    margin-bottom: 52px;\n}\n\n.eye,\n.chevron-down {\n    margin-right: 10px;\n}\n\n.login-button {\n    width: 100%;\n    height: 48px;\n    font-size: 20px;\n    font-family: \"Roboto\";\n    font-weight: normal;\n    color: #ffffff;\n    background-color: #151233;\n    border: none !important;\n    border-radius: 4px;\n}\n\n.next-button {\n    width: 40%;\n    height: 48px;\n    font-size: 20px;\n    font-family: \"Roboto\";\n    font-weight: normal;\n    color: #ffffff;\n    background-color: #151233;\n    border-radius: 4px;\n}\n\n.valid {\n    color: #00ff00;\n    font-family: \"Roboto\";\n    font-size: 12px;\n}\n\n.invalid {\n    color: #ff0000;\n    font-family: \"Roboto\";\n    font-size: 12px;\n}\n\n.form-control:focus {\n    background-color: #eeeeee !important;\n    box-shadow: none !important;\n}\n\n .form-control:disabled, .form-control[readonly]{\n     opacity: 1;\n }\n\n @media (max-width: 1500px){\n    .login-background {\n      width: 55%;\n    }\n  }\n\n  @media (max-width: 1300px){\n    .login-background {\n      width: 50%;\n    }\n  }\n\n  @media (max-width: 1160px){\n    .login-background {\n      width: 45%;\n    }\n  }\n\n  @media (max-width: 1000px){\n    .login-background {\n      display: none;\n    }\n    .form-wrap{\n      margin-top: 216px;\n    }\n  }\n\n</style>\n"],"sourceRoot":""}]);
